import React from "react";
import { Button, Form, FormGroup, Label, Input, FormText } from "reactstrap";
import MailchimpSubscribe from "react-mailchimp-subscribe";
import { Formik } from "formik";

const CustomForm = ({ status, message, onSubmit, ...props }) => {
  return (
    <>
      <p>{status}</p>

      <p>{message}</p>

      <Formik
        initialValues={{ EMAIL: "" }}
        onSubmit={(values, _a) => onSubmit(values)}
        render={props => (
          <Form onSubmit={props.handleSubmit}>
            <FormGroup>
              <Input
                placeholder="joe@example.com"
                type="email"
                onChange={props.handleChange}
                onBlur={props.handleBlur}
                value={props.values.EMAIL}
                name="EMAIL"
              />
            </FormGroup>

            {props.errors.email && (
              <div id="feedback">{props.errors.email}</div>
            )}

            <Button color="primary" block type="submit">
              Subscribe
            </Button>
          </Form>
        )}
      />
    </>
  );
};

export const MailChimpForm = props => {
  const url =
    "https://twitter.us14.list-manage.com/subscribe/post?u=c995eaf6129ecf4b13d8ae469&id=dd2d98db0f";

  return (
    <div>
      <MailchimpSubscribe
        url={url}
        render={({ subscribe, status, message }) => (
          <CustomForm
            status={status}
            message={message}
            onSubmit={async formData => await subscribe(formData)}
          />
        )}
      />
    </div>
  );
};
